___CSS_LOADER_EXPORT___.push([module.id, `.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal .content {
  background-color: #4d4d4d;
  margin: 15% auto;
  padding: 20px 0;
  border-radius: 5px;
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: bold;
}

.box-btn {
  display: flex;
  margin-top: 20px;
  justify-content: center;
}

.form-cadastro {
  width: 90%;
}

.form-cadastro input,
.form-cadastro select {
  margin: 5px 0;
  font-size: 16px;
  border-radius: 10px;
  border: none;
  color: #212121;
}

.form-cadastro select {
  padding: 7px;
}

button {
  padding: 10px;
  border: none;
  font-weight: bold;
  cursor: pointer;
  border-radius: 10px;
}

.btn-fechar {
  margin-right: 10px;
  background: #ef5350;
  color: #fefefe;
}

.btn-cadastrar {
  background: #0f7f3c;
  color: #fefefe;
}
